define([
	"./modals"
],function(modals){
  // the base DOM structure needed to create a modal
  var templates = {
    dialog:
      "<div class='modals modal' tabindex='-1' role='dialog'>" +
        "<div class='modal-dialog'>" +
          "<div class='modal-content'>" +
            "<div class='modal-body'><div class='modals-body'></div></div>" +
          "</div>" +
        "</div>" +
      "</div>",
    header:
      "<div class='modal-header'>" +
        "<h4 class='modal-title'></h4>" +
      "</div>",
    footer:
      "<div class='modal-footer'></div>",
    closeButton:
      "<button type='button' class='modals-close-button close' data-dismiss='modal' aria-hidden='true'>&times;</button>",
    form:
      "<form class='modals-form'></form>",
    inputs: {
      text:
        "<input class='modals-input modals-input-text form-control' autocomplete=off type=text />",
      textarea:
        "<textarea class='modals-input modals-input-textarea form-control'></textarea>",
      email:
        "<input class='modals-input modals-input-email form-control' autocomplete='off' type='email' />",
      select:
        "<select class='modals-input modals-input-select form-control'></select>",
      checkbox:
        "<div class='checkbox'><label><input class='modals-input modals-input-checkbox' type='checkbox' /></label></div>",
      date:
        "<input class='modals-input modals-input-date form-control' autocomplete=off type='date' />",
      time:
        "<input class='modals-input modals-input-time form-control' autocomplete=off type='time' />",
      number:
        "<input class='modals-input modals-input-number form-control' autocomplete=off type='number' />",
      password:
        "<input class='modals-input modals-input-password form-control' autocomplete='off' type='password' />"
    }
  };

  return modals.templates = templates;
});